  body {
    background: black;
    overflow-x: hidden;

  }

  .container-fluid {

    display: flex;
    /* width: 100%; */
    flex-direction: column;
    height: 850px;
    align-items: center;
    /* background-image: url(./13.jpg); */
    background-size: 100% 100%;

  }

  .timer-container {
    color: #fff;
    margin: 0 auto;
    text-align: center;
  }

  .timer-container li {
    display: inline-block;
    /* font-size: 1.2em; */
    list-style-type: none;
    padding: 1em;
    text-transform: uppercase;
    /* background-color:black; */
  }

  

  .timer-container .emoji {
    display: none;
    padding: 1rem;
  }

  .timer-container .emoji span {
    font-size: 4rem;
    padding: 0 .3rem;
  }

  .logo {
    width: 220px;
    height: 220px;
    /* border: 1px solid red; */
    background-image: url(../../public/logo.png);
    background-size: 100% 100%;

  }

  #headline {
    font-size: 1rem;
    display: none;
    /* color: white; */
  }



  /* @media screen and (max-width: 620px) {

    .timer-container li {
      font-size: .4rem;
    }

    .timer-container li span {
      font-size: 2rem;
    }


  } */

  .text h1 {
    color: pink;
    box-shadow: 20px 10px 20px 0px white;
    animation: color 3s infinite;

  }

  @keyframes color {
    20% {
      color: green;
      box-shadow: 20px 0px 20px 0px green;
    }

    40% {
      color: Yellow;
      box-shadow: 20px 0px 20px 0px yellow;
    }

    60% {
      color: orange;
      box-shadow: 20px 0px 20px 0px orange;
    }

    80% {
      color: red;
      box-shadow: 20px 0px 20px 0px red;
    }

    100% {
      color: aqua;
      box-shadow: 20px 0px 20px 0px aqua;
    }
  }

  .animation{
    animation: move 3s infinite;

  }



  .cover {
    display: flex;
    justify-content: center;
    align-items: center;
    
    margin: 0;
}

.image-row {
    display: flex;
    gap: 10px; /* Space between images */
}

.image-row img {
    width: 150px;
    height: 150px;
    animation: moveY 12s infinite;
}

@keyframes moveY {
    0% {
        transform: translateX(0px);
    }
    100% {
        transform: translateX(-1000px);
    }
}

